.nav {
  display: flex;
  align-items: center;
  padding: 20px 70px;
  background-color: #fe6b64;
  font-family: "Overpass", serif;
  font-weight: 700;
  color: white;
  position: relative;
  padding-top: 2rem;
}

.hamburger {
  display: none;
  background: none;
  border: none;
  color: white;
  font-size: 24px;
  cursor: pointer;
}

.closeIcon {
  color: #333;
  font-size: 24px;
}

.menu {
  display: flex;
  gap: 30px;
  align-items: center;
  width: 100%;
}

.ul1,
.ul2 {
  display: flex;
  gap: 20px;
  align-items: center;
  list-style: none;
}

.ul2 {
  margin-left: auto;
}

.logoLink {
  text-decoration: none;
  color: white;
}

.logo {
  font-size: 30px;
  margin: 0;
  color: #f0f0f0;
}

.navLink {
  color: white;
  text-decoration: none;
  font-weight: 600;
  padding: 10px;
}

.navLink:hover {
  text-decoration: underline;
}

.menuButton {
  background: none;
  border: none;
  color: white;
  font-family: "Overpass", serif;
  font-weight: 600;
  font-size: 16px;
  padding: 10px;
  cursor: pointer;
}

.menuButton:hover {
  text-decoration: underline;
}

.loginButton {
  background: transparent;
  border: 2px solid white;
  border-radius: 50px;
  padding: 8px 20px;
  cursor: pointer;
}

.loginButton:hover {
  background: rgba(255, 255, 255, 0.2);
}

.signupButton {
  background: white;
  border: 2px solid white;
  border-radius: 50px;
  padding: 8px 20px;
  cursor: pointer;
}

.signupButton:hover {
  background: #f0f0f0;
}

.authLink {
  color: white;
  text-decoration: none;
  font-weight: 600;
}

.signupButton .authLink {
  color: #fe6b64;
}

.signupButton .authLink:hover {
  color: #ff3333;
}

.dropdownContainer {
  position: relative;
}

.dropdown {
  position: absolute;
  top: 100%;
  left: 0;
  background-color: white;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  gap: 10px;
  z-index: 1000;
  min-width: 200px;
}

.dropdown a {
  color: #333;
  text-decoration: none;
  font-weight: 600;
  padding: 5px 10px;
}

.dropdown a:hover {
  color: #fe6b64;
}

@media (max-width: 768px) {
  .logo {
    color: #fe6b64;
  }

  .nav {
    padding: 20px;
    flex-direction: column;
    align-items: flex-start;
  }
  .hamburger {
    display: block;
    position: absolute;
    top: 20px;
    right: 20px;
  }
  .menu {
    display: none;
    flex-direction: column;
    width: 100%;
    background-color: #f9f9f9;
    padding: 20px;
    border-radius: 5px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  .menuOpen {
    display: flex;
  }
  .ul1,
  .ul2 {
    flex-direction: column;
    width: 100%;
    gap: 15px;
  }
  .ul2 {
    margin-left: 0;
    align-items: center;
  }
  .navLink,
  .menuButton {
    color: #333;
    width: 100%;
    text-align: center;
    padding: 10px;
  }
  .navLink:hover,
  .menuButton:hover {
    color: #fe6b64;
  }
  .dropdownContainer {
    width: 100%;
  }
  .dropdown {
    position: static;
    width: 100%;
    background-color: #ffffff;
    border-radius: 0;
    box-shadow: none;
    padding: 10px 20px;
  }
  .dropdown a {
    color: #555;
    text-align: center;
  }
  .dropdown a:hover {
    color: #fe6b64;
  }
  .loginButton,
  .signupButton {
    width: 100%;
    text-align: center;
    padding: 8px 0;
  }
  .authLink {
    color: #333;
  }
  .signupButton .authLink {
    color: #fe6b64;
  }
}

@media (max-width: 480px) {
  .nav {
    padding: 10px;
  }
  .logo {
    font-size: 24px;
    color: #fe6b64;
  }
  .navLink,
  .menuButton,
  .authLink,
  .dropdown a {
    font-size: 14px;
  }
  .hamburger {
    top: 15px;
    right: 15px;
    font-size: 20px;
  }
}
