.container {
  font-family: 'Overpass', serif;
  color: #333;
}

.header {
  background-color: #fe6b64;
  border-bottom-left-radius: 300px;
  padding: 70px;
  text-align: center;
  color: white;
}

.pg {
  font-size: 14px;
}

.main {
  padding: 50px;
  display: flex;
  flex-direction: column;
  gap: 40px;
  align-items: center;
}

.section {
  display: flex;
  align-items: center;
  gap: 40px;
  max-width: 1200px;
  width: 100%;
}

.text {
  max-width: 600px;
}

.text h3 {
  margin-bottom: 20px;
}

@media (max-width: 768px) {
  .header {
    padding: 20px;
    border-bottom-left-radius: 100px;
  }
  .main {
    padding: 20px;
  }
  .section {
    flex-direction: column;
    text-align: center;
  }
  img {
    max-width: 100%;
    height: auto;
  }
}

@media (max-width: 480px) {
  .header {
    padding: 15px;
  }
  .pg {
    font-size: 12px;
  }
  .text h3 {
    font-size: 18px;
  }
}