* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  max-width: 1440px;
  background-color: white;
  margin: 0 auto;
}

.header {
  background-color: #fe6b64;
  border-bottom-left-radius: 300px;
  padding: 70px;
  font-family: "Overpass", serif;
  font-optical-sizing: auto;
  font-weight: 700;
  font-style: normal;
  color: white;

}

.h_div {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 80px;
  gap: 10px;
}

.pg {
  font-size: 14px;
}

.f_div {
  display: flex;
  gap: 30px;
  align-items: center;
  justify-content: center;
}

.main {
  background-color: white;
  display: grid;
  grid-template-columns: repeat(2, auto);
  grid-template-rows: repeat(7, auto);
  font-family: "Overpass", serif;
  font-optical-sizing: auto;
  font-weight: 200;
  font-style: normal;
}

.h2_m {
  grid-row: 1/2;
  grid-column: 1/3;
  text-align: center;
  height: 100px;
  padding-top: 50px;
  margin-bottom: 50px;
}

.div_1 {
  grid-row: 2/3;
  grid-column: 1/2;
  margin-left: 60px;
  margin-right: 60px;
}

.div_2 {
  grid-row: 3/4;
  grid-column: 1/2;
  margin-left: 60px;
  margin-right: 60px;
}

.div_3 {
  grid-row: 4/5;
  grid-column: 2/3;
  height: 300px;
  align-self: center;
  padding-top: 100px;
  padding-right: 200px;
  background-color: #383955;
  margin-top: 100px;
  border-top-right-radius: 50px;
  color: white;
}

.div_4 {
  grid-row: 5/6;
  grid-column: 2/3;
  margin-top: 100px;
}

.div_5 {
  grid-row: 6/7;
  grid-column: 2/3;
}

.img_1 {
  grid-column: 2/3;
  grid-row: 2/4;
  margin: auto;
}

.img_2 {
  grid-column: 1/2;
  grid-row: 4/5;
  padding-left: 250px;
  background-color: #383955;
  height: 300px;
  margin-top: 100px;
  border-bottom-left-radius: 50px;
}

.img_3 {
  grid-column: 1/2;
  grid-row: 5/7;
  margin-top: 150px;
  margin: auto;
}

img {
  max-width: 100%;
  height: auto;
}

@media (max-width: 768px) {
  .header {
    background-color: #fe6b64;
    padding: 20px;
    border-bottom-left-radius: 50px;
  }
  .h_div {
    margin: 30px 0;
  }
  .pg {
    font-size: 14px;
  }
  .f_div {
    flex-direction: column;
    gap: 10px;
  }
  .main {
    display: flex;
    flex-direction: column;
    gap: 20px;
    padding: 20px;
  }
  .h2_m {
    font-size: 24px;
    padding-top: 20px;
    margin-bottom: 20px;
  }
  .div_1, .div_2, .div_3, .div_4, .div_5 {
    max-width: 600px;
    margin: 0 auto;
    text-align: center;
    padding: 10px;
  }
  .div_3 {
    background-color: #383955;
    padding: 20px;
    border-radius: 10px;
    margin-top: 0;
    height: auto;
  }
  .img_1, .img_2, .img_3 {
    margin: 10px 0;
    padding-left: 0;
    background-color: transparent;
    border-radius: 0;
    height: auto;
  }
}

@media (max-width: 480px) {
  .header {
    padding: 15px;
  }
  .pg {
    font-size: 12px;
  }
  .h2_m {
    font-size: 20px;
  }
}