.container {
  font-family: 'Overpass', serif;
  color: #333;
}

.header {
  background-color: #fe6b64;
  border-bottom-left-radius: 300px;
  padding: 70px;
  text-align: center;
  color: white;
}

.pg {
  font-size: 14px;
}

.main {
  padding: 50px;
  display: flex;
  justify-content: center;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 20px;
  max-width: 400px;
  width: 100%;
}

.formGroup {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.formGroup label {
  font-weight: 600;
}

.formGroup input {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-family: 'Overpass', serif;
}

@media (max-width: 768px) {
  .header {
    padding: 20px;
    border-bottom-left-radius: 100px;
  }
  .main {
    padding: 20px;
  }
}