.button {
  background-color: #fe6b64;
  border: 0;
  color: white;
  font-family: 'Overpass', serif;
  font-weight: 600;
  padding: 10px 20px;
  cursor: pointer;
  border-radius: 50px;
  margin-top: 1rem;
}

.bb {
  background-color: #eaeaec;
  color: #fe6b64;
}

.button:hover {
  opacity: 0.9;
}

@media (max-width: 768px) {
  .button {
    padding: 8px 16px;
  }
}