.footer {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  background-color: #24242c;
  font-family: 'Ubuntu', serif;
  font-weight: 300;
  font-size: 14px;
  padding: 40px;
  border-top-right-radius: 100px;
  margin-top: auto;
}

.h1_f {
  color: white;
  margin-left: 50px;
  margin-top: 30px;
  font-size: 30px;
}

.div1_f,
.div2_f,
.div3_f {
  display: flex;
  flex-direction: column;
  gap: 15px;
  color: white;
  padding: 30px;
}

.h2_f {
  font-size: 16px;
  margin-bottom: 15px;
}

.fa {
  color: #c3c3cb;
  text-decoration: none;
  font-size: 14px;
}

.fa:hover {
  color: #fe6b64;
}

@media (max-width: 768px) {
  .footer {
    grid-template-columns: 1fr;
    text-align: center;
    padding: 20px;
  }
  .h1_f {
    margin-left: 0;
    margin-bottom: 20px;
  }
  .div1_f, .div2_f, .div3_f {
    padding: 20px;
    gap: 10px;
  }
}

@media (max-width: 480px) {
  .footer {
    font-size: 12px;
  }
  .h1_f {
    font-size: 24px;
  }
  .h2_f {
    font-size: 14px;
  }
  .fa {
    font-size: 12px;
  }
}